import Song, { play_song } from "./song.js";

export default class Player {
    constructor(map) {
        Object.entries(map);
        let aux = 1;
        for (var [key, value] of Object.entries(map)) {
            // Instanciar song
            const song = new Song(key, value, `.cv${aux}`);
            // Funci贸n de reproducci贸n de canci贸n
            play_song(song);
            aux++;
        }
    }
}